$backgroundColor: #eee;
$backgroundColorHover: pink;
$gapcontainer: 1px;
$calendarPaddings: 10px;
$calendar__dayWidth: 100px;
$widthOfCalendar:
$calendar__dayWidth * 7 + 2 * $calendarPaddings + 6 * $gapcontainer;
$width: $calendar__dayWidth * 7 + 6 * $gapcontainer;
$dayOfTheWeek: (
  "mon": 7,
  "tue": 6,
  "wed": 5,
  "thu": 4,
  "fri": 3,
  "sat": 2,
  "sun": 1,
);
